     //  
    Boxing_print() {
      var Numcopies=null
      if(this.warehouseID.indexOf('SZ')!=-1){ //
        if(this.waybillinfo.Type == 4||this.waybillinfo.Type == 3){
           this.language = 'SC'
           Numcopies=2
        }else{
           this.language = 'SC'
          Numcopies=4
        }
      }else{ //
          if (this.waybillinfo.Type == 4) {//
             this.language = 'EN'
             Numcopies=2
          } else if(this.waybillinfo.Type == 3){//
            this.language = 'TC'
            Numcopies=2
          }else{
             this.language = 'TC'
             Numcopies=4
          }
      }
      var data = {
        Language: this.language,
        waybillinfo: this.waybillinfo,
        listdata:this.detailitem,
        Numcopies:Numcopies
      }
      PrintDeliveryList(data)
    },
